  border-radius: 50%;
}

.l--go::before, .l--go::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  transition: inherit;
  border-radius: 50%;
}

