.s--btn {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f7f5e9;
  width: 5.5vw;
  min-width: 75px;
  height: 5.5vw;
  min-height: 75px;
  text-transform:lowercase;
  font-family: "FuturaBkBT";
  cursor: pointer;
  margin-top: 3vw;
  text-decoration: none;
  position: relative;
  z-index: 1;
}

.s--btn label {
  opacity: 0;
  position: absolute;
  width: 8.5vw;
  min-width: 150px;
  left: 0;
  text-align: center;
  transform: scale(.7);
  transition: opacity .2s ease 0s, transform .2s ease 0s;
  pointer-events: none;
}

.s--btn:hover label {
  transform: scale(1);
  opacity: 1;
  transition: opacity .4s ease .2s, transform .4s ease .2s;
}

.s--btn::before{
  content: '';
  position: absolute;
  width: 5.5vw;
  min-width: 75px;
  height: 5.5vw;
  min-height: 75px;
  border-radius: 10vw;
  z-index: -1;
  left: 0;
}

.s--btn-next, .s--btn-back, .s--btn-prev {
  position: absolute;
  left: 0;
  width: 5.5vw;
  min-width: 75px;
  height: 5.5vw;
  min-height: 75px;
  pointer-events: none;
  transition: .3s ease;
}

.s--btn-next {
  background: url(./assets/img/btn/next-btn.svg) no-repeat 50% / 40%;
}
.s--btn-back {
  background:url(./assets/img/btn/home-btn.svg) no-repeat 50% / 40%;
}
.s--btn-prev {
  background: url(./assets/img/btn/back-btn.svg) no-repeat 50% / 40%;
}

.s--btn:hover .s--btn-next, .s--btn:hover .s--btn-back, .s--btn:hover .s--btn-prev {
  opacity: 0;
  transition:transform .6s cubic-bezier(.68, -0.55, .27, 1.70), opacity .4s ease .1s;
}

.s--btn:hover .s--btn-next {
  transform: translateX(93%);
}
.s--btn:hover .s--btn-back {
  transform: translateY(-93%);
}
.s--btn:hover .s--btn-prev {
  transform: translateX(-93%);
}

.s--btn::before {
  background: #da554b;
  transition: .5s ease;
  pointer-events: none;
}
/* .s--btn::after {
  background: #333;
  transform: translateY(100%);
} */

