button.nav--btn {
  position: fixed;
  z-index: 6;
  top: 3vw;
  right: 12vw;
  border: none;
  width: 2.7vw;
  min-width: 50px;
  height: 2.7vw;
  min-height: 50px;
  color: #f9f9f9;
  padding: .8vw 1.2vw;
  cursor: pointer;
  display: flex;
  transition: .7s cubic-bezier(.68, -0.55, .27, 1.70);
  justify-content: center;
  align-items: center;
  background: transparent;
}

button.nav--btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #333;
  border-radius: 20vw;
  transform: scale(1);
  transition: inherit;
}

button.nav--btn::after {
  content: '';
  position: absolute;
  top: -15%;
  left: -15%;
  width: 130%;
  height: 130%;
  background: #f7f5e9;
  border-radius: 20vw;
  transform: scale(1);
  z-index: -1;
  transition: inherit;
}

button.nav--btn:hover::before, .act button.nav--btn::before {
  transform: scale(1.55);
  background: #333;
}
.act button.nav--btn::before{
  transform: scale(1.2);
}

button.nav--btn:hover::after {
  transform: scale(.5);
} 

.act button.nav--btn::after {
  transform: scale(.5);
}

button.nav--btn svg {
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  fill: #f9f9f9;
  transition: .6s ease;
}

button.nav--btn .a-m--g1,
button.nav--btn .a-m--g2,
button.nav--btn .a-m--g3 {
  transform: rotate(0deg) translate3D(0, 0, 0);
  object-position: center;
  transition: .6s ease;
}
 
.act button.nav--btn .a-m--g1 {
  transform: rotate(45deg) translate3D(22%, -22%, 0);
}

.act button.nav--btn .a-m--g2 {
  transform: translateX(50%);
  opacity: 0;
}

.act button.nav--btn .a-m--g3 {
  transform: rotate(-45deg) translate3D(-51%, -2%, 0);
}

#nav--menu {
  width: 100%;
  height: 100%;
  background: #f7f5e9;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 5;
  visibility: hidden;
  transition: opacity .5s ease-in .25s, visibility 0s .8s;
  user-select: none;
  opacity: 0;
  font-family: "FuturaBkBT";
  pointer-events: none;
}

#nav--menu::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #f7f5e9;
}

.act #nav--menu {
  visibility: visible;
  pointer-events: painted;
  opacity: 1;
  transition: visibility 0s, opacity .6s ease-out;
}

.no-act #nav--menu {
  visibility: hidden;
  transition:opacity .3s ease-out, visibility 0s .3s;

}
.no-act button.nav--btn{
  transition: none;
}

#nav--menu .c-right {
  position: absolute;
  bottom: 2vw;
  width: auto;
  height: auto;
  color: #333;
  cursor: default;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: calc(10px + .4vw);
  transform: scale(.9);
  transition: .4s ease-out;
  line-height: calc(12px + .4vw);
  opacity: 0;
}

.act #nav--menu .c-right {
  transform: scale(1);
  transition: .8s cubic-bezier(.68, -0.55, .27, 1) .9s;
  opacity: 1;
}

.nav--m-c{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content:center;
  overflow: hidden;
  pointer-events: none;
}

.act .nav--m-c {
  pointer-events: painted;
}

.nav--m-c .navigation, .nav--m-c .contact {
  position: relative;
  width: auto;
  height: auto;
  top: -1vw;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: .4s ease-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1.5vw 0;
  transform: scale(.9);
  /* background: cornsilk; */
}


.act .nav--m-c .navigation {
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  transition:transform .8s ease .2s, opacity .8s ease .2s;
}

.act .nav--m-c .contact {
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  transition:transform .8s ease 1s, opacity .8s ease 1s;
}

.nav--m-c .navigation ul {
  width: 100%;
  height: auto;
  text-align: center;
}

.nav--m-c .navigation ul li {
  position: relative;
  list-style-type: none;
  display: block;
  margin: 0;
  padding: 0;
}

.nav--m-c .navigation ul li:nth-child(1),
.nav--m-c .navigation ul li:nth-child(2),
.nav--m-c .navigation ul li:nth-child(3),
.nav--m-c .navigation ul li:nth-child(4) {
  opacity: 0;
  transform: scale(.9);
  transition: .7s ease;
}

.act .nav--m-c .navigation ul li:nth-child(1) {
  opacity: 1;
  transform: scale(1);
  transition: .7s ease .4s;
}

.act .nav--m-c .navigation ul li:nth-child(2) {
  opacity: 1;
  transform: scale(1);
  transition: .7s ease .6s;
}

.act .nav--m-c .navigation ul li:nth-child(3) {
  opacity: 1;
  transform: scale(1);
  transition: .7s ease .8s;
}

.act .nav--m-c .navigation ul li:nth-child(4) {
  opacity: 1;
  transform: scale(1);
  transition: .7s ease 1s;
}

.nav--m-c .navigation ul li a {
  position: relative;
  color: #333;
  text-decoration: none;
  width: auto;
  display: inline-block;
  font-weight: 700;
  text-transform: uppercase;
  line-height: calc(30px + 2.8vw);
  /* font-size: calc(28px + 1.2vw); */
  font-size: calc(30px + 2.8vw);
  transition: .5s ease;
  letter-spacing: -.1vw;
