.nav--m-c .contact ul {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:center;
  align-items:stretch;
}
.nav--m-c .contact ul li {
  margin:.3vw .4vw;
}

.nav--m-c .contact ul li a{
  width: 2.3vw;
  min-width: 40px;
  height: 2.3vw;
  min-height: 40px;
  display:flex;
  justify-content: center;
  align-items: center;
  transform: scale(1);
}

.nav--m-c .contact ul li a img{
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
}

.nav--m-c .contact ul li:nth-child(1) a::before {
  background-color: #4E9CB0;
}

.nav--m-c .contact ul li:nth-child(4) a {
  background-size: 30%;
}

.nav--m-c .contact ul li a::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #333;
  border-radius: 100%;
  transition: .5s cubic-bezier(.68, -0.55, .27, 1.90);
}

.nav--m-c .contact ul li a:hover::before{
  transform: scale(1.15);
  background: #da554b;
}

.btnfull {
  position: absolute;
  bottom: 3.5vw;
  right: 12.4vw;
  width: 2.7vw;
  min-width: 50px;
  height: 2.7vw;
  min-height: 50px;
  z-index: 2;
  opacity: 0;
  transition: .5s ease;
  transform: scale(.8);
}

.act .btnfull {
  opacity: 1;
  transform: scale(1);
  transition: .7s ease .4s;
}

.btnfull div{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.btnfull div::after, .btnfull div::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: .5s cubic-bezier(.68, -0.55, .27, 1.90);
}

.btnfull div::after {
  background: #da554b url(./assets/img/fullScreen.svg) no-repeat 50% / 30px;
}

.btnfull div::before{
  background: #333;
  transform: scale(.9);
}

.btnfull div:hover::before{
  transform: scale(1.2);
}

.btnfull div:hover::after{
  background: #333 url(./assets/img/fullScreen.svg) no-repeat 50% / 30px;
}

.tr--1 {
  position: absolute;
  width:calc(22px + 97vh);
  height: 100%;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.tr--2 {
  position: absolute;
  width:calc(22px + 97vh);
  top: 0;
  right: 0;
  height: 100%;
}

.tr--1 svg, .tr--2 svg {
  position: absolute;
  width: 100%;
  height: auto;
  right: 0;
}

.tr--1 svg{bottom: -23%;}
.tr--2 svg{top: 0;}

.h--quote-cont {
  position: absolute;
  top: 0;
  left: 10.7vw;
  width: 40vw;
  height: 100vh;
  z-index: 3;
  pointer-events: none;
  user-select: none;
}

.h--quote-cont div{
 width: 100%;
 height: 100%;
 top: 0;
 left: 0;
 display: flex;
 justify-content: center;
 align-items: center;
}

.h--quote {
  position: absolute;
  width: 100%;
  left: 0;
  height: auto;
  z-index: 2;
  pointer-events: none;
  user-select: none;
}

.page-nav-scrl{ 
  position: absolute;
  top: 0;
  right: 12.6vw;
  z-index: 99;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "FuturaBkBT";
}
.page-nav-scrl a {
  text-decoration: none;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: .4vw;
  height: .4vw;
  min-width: 30px;
  min-height: 30px;
  margin: .65vw 0;
  opacity: 1;
  transition: .5s ease;
  pointer-events: painted;
}
.page-nav-scrl a::before{
  content: '';
  position: absolute;
  width: 50%;
  height: 50%;
  background: #333;
  border-radius: 50%;
  transform: scale(.6);
  z-index: 1;
  transition: .3s ease-out;
}
.page-nav-scrl a::after{
  content: '';
  position: absolute;
  width: 50%;
  height: 50%;
  background: #f7f5e9;
  border-radius: 50%;
  transform: scale(.5);
  z-index: 0;
  transition: .5s cubic-bezier(.68,-0.55,.27,1.90);
}
.page-nav-scrl a:hover::before, .page-nav-scrl a.active::before {
  background: #f6c04b;
  transform: scale(1);
}
.page-nav-scrl a:hover::after, .page-nav-scrl a.active::after {
  transform: scale(2);
}
.page-nav-a{
  opacity: 1;
  transition: .5s ease;
}
.page-nav-scrl a label{
 visibility: hidden;
 pointer-events: none;
 user-select: none;
 position: absolute;
  width: auto;
  top: 0;
  left: 0;
  transform: translate(4vw, 0);
  font-size: calc(10px + .3vw);
  color: #333;
  line-height:calc(10px + .3vw) ;
  text-transform: lowercase;
  background: #f7f5e9;
  padding: .4vw .6vw;
  border-radius: 20vw;
  opacity: 0;
  transition: .5s cubic-bezier(.68,-0.55,.27,1.90) .2s;
}
.page-nav-scrl a:hover label, .page-nav-scrl a.active label {
  visibility: visible;
  opacity: 1;
  transform: translate(2.5vw, 0);
  pointer-events:painted;
}

